<template>
  <div>
    <el-container>
      <el-aside style="width: 200px;min-height: 100vh ;background-color: #001529">
        <div style="height: 60px;color: white;display:flex; align-items: center;justify-content: center">
          企业工作台
        </div>
        <el-menu :default-active=$route.path active-text-color="#fff" background-color="#001529" router
                 style="border: none;background-color: red" text-color="rgba(255,255,255,0.65)">
          <el-menu-item index="/componentHomeView">
            <template slot="title">
              <i class="el-icon-house"></i>
              <span>系统首页</span>
            </template>
          </el-menu-item>
          <el-menu-item index="/componentInterview">面试通知</el-menu-item>

          <el-submenu>
            <template slot="title">
              <span>信息管理</span>
            </template>
            <el-menu-item index="companyInformation">查看/修改企业信息</el-menu-item>
            <el-menu-item index="componentPosts">查看所有岗位</el-menu-item>
            <el-menu-item index="companyPost">发布岗位</el-menu-item>
          </el-submenu>
          <el-menu-item index="/">用户退出</el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header></el-header>
        <el-main>
          <el-table border :data="interview" >
            <el-table-column  label="职位名称" prop="recruitmentPosition"/>
            <el-table-column label="职位分类" prop="jobClassification"/>
            <el-table-column label="招聘人数" prop="recruitingNumbers"/>
            <el-table-column  label="工资" prop="wage"/>
            <el-table-column label="工作地点" prop="workPlace"/>
            <el-table-column label="职位要求" prop="jobRequirements"/>

            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button type="success" @click="update(scope.row.id)">修改职位</el-button>
                <br><br>
                <el-button type="warning" @click="del(scope.row.id)">删除职位</el-button>
              </template>

            </el-table-column>
          </el-table>
        </el-main>
      </el-container>

    </el-container>


  </div>
</template>
<style>
.el-menu--inline .el-menu-item {
  background-color: #000c17 !important;
}

.el-menu-item:hover, .el-submenu__title:hover {
  color: #fff !important;
}

.el-submenu__title:hover .el-icon-menu {
  color: #fff !important;
}

.el-menu-item.is-active {
  background-color: red !important;
}


</style>


<script>
import request from "@/utils/request";

export default {
  name: 'componentPosts',
  data() {
    return {
      interview: []
    }
  },
  methods: {
    del(id) {
      request.get('/component/deletePost/' + id).then(res => {
        this.$message.success("删除成功")
        location.reload()
      })
    }, update(id) {
      this.$router.push({
        path: '/companyUpdatePost',
        query: {
          id
        }
      })
      this.$message.success("查看成功")
    },
  },
  mounted() {
    request.get('/component/allPost').then(res => {
      console.log(res.data.data);
      this.interview = res.data.data
    })
  }
}
</script>
